form {
    max-width : 600px;
    margin : 20px auto;
    padding : 20px;
    border : #ccc solid 1px;
    border-radius : 30px;
    box-shadow : 0 0 10px rgb(0, 0, 0, 0.1);
    }
  /* Add styles to the form container */
    .form-container {
    max-width : 300px;
    padding : 10px;
    background-color : white;
    }
  /* Full-width textarea */
    .form-container textarea {
    width : 100%;
    padding : 15px;
    margin : 5px 0 22px 0;
    border : none;
    background : #f1f1f1;
    resize : none;
    min-height : 200px;
    }
  /* When the textarea gets focus, do something */
    .form-container textarea:focus {
    background-color : #ddd;
    outline : none;
    }
   /* Set a style for the submit/send button */
    .form-container .btn {
    background-color : #4e6783;
    color : white;
    padding : 16px 20px;
    border : none;
    cursor : pointer;
    width : 100%;
    margin-bottom : 10px;
    opacity : 0.8;
    }
  /* Add a red background color to the cancel button */
    .form-container .cancel {
    background-color : rgb(24, 22, 22);
    }
  /* Add some hover effects to buttons */
    .form-container .btn:hover, .open-button:hover {
    opacity : 1;
    }

 /* Add styles to the form container */
 .form-container {
    max-width : 300px;
    padding : 10px;
    background-color : white;
    }

/* Navigation Button Styles */
.nav-button {
  background-color: #2c3d4f;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  margin-right: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.nav-button:hover {
  background-color: #0056b3;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  .nav-button {
    margin: 5px 0;
  }
}

/* Create a sidebar style */
.sideBar {
  margin-top: 58px;
  background-color: #cecece;
  color: black;
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  overflow-y: auto;
  transition: width 0.3s;
}

.sideBar a {
  text-decoration: none;
  display: block;
  padding: 15px;
  color: black;
  transition: background-color 0.3s;
}

.sideBar a:hover {
  background-color: #2c3d4f;
  color: white;
}

.content {
  margin-left: 250px;
  padding: 20px;
}

/* No UnderLine */
.no-underline {
  text-decoration: none;
